* {
  box-sizing: border-box;
  vertical-align: baseline;
  background: none;
  outline: 0;
  padding: 0;
  font-size: 100%;
}

body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.gallery {
  display: none;
}

.photo-card {
  min-width: 300px;
  flex-wrap: wrap;
